.IssueList .section-header {
  margin: 60px 0 20px 0;
  font-size: 14px;
  font-weight: bold;
}

.IssueList table {
  border-collapse: collapse;
}

.IssueList th {
  font-size: 12px;
  padding: 8px 16px;
  border: 1px solid #cecece;
  background-color: #efefef;
}

.IssueList td {
  height: 56px;
  min-height: 56px;
  font-size: 14px;
  line-height: 18px;
  padding: 8px 16px;
  border: 1px solid #cecece;
}

.IssueList table .title {
  width: 500px;
  max-width: 500px;
  min-width: 500px;
  overflow: hidden;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
}

.IssueList table .state {
  width: 56px;
  text-align: center;
}

.IssueList table .labels {
  width: 96px;
  text-align: center;
}

.IssueList td.state {
  color: #20b030;
}

.IssueList td a {
  color: rgba(0,0,0,0.87);
  text-decoration: none;
}

.IssueList td a:hover {
  text-decoration: underline;
}

.IssueList td .avatar {
  margin: 4px 4px 0 0;
  width: 24px;
  height: 24px;
  border-radius: 6px;
}

.IssueList .refnum {
  color: #a0a0a0;
}

.IssueList .label {
  padding: 4px;
  font-size: 10px;
  line-height: 10px;
  font-weight: bold;
  color: white;
  display: inline-block;
  border-radius: 2px;
  margin: 2px;
  overflow: hidden;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
}


