.App {
  text-align: initial;
  background-color: white;
}

.App-header {
  position: relative;
  text-align: center;
  background-color: #3c4146;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: 100% 100%;
  height: 84px;
  margin:0;
  padding:0;
}

.App-header-content {
  position: absolute;
  width: 900px;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin-left: auto;
  margin-right: auto;
  padding: 20px;
}

.App-header-rightbox {
  position: absolute;
  top:26px;
  right:20px;
  bottom:26px;
  color: white;
}

.App-header-rightbox .material-icons {
  font-size: 32px;
}

.App-title {
  margin: 0;
  text-align: left;
  color: white;
  font-weight: 200;
  font-size: 24px;
  line-height: 44px;
}

.App-main {
  width: 900px;
  margin: auto;
  padding: 40px 40px 40px 40px;
}

.App-config table {
  margin-bottom: 40px;
  border-collapse: collapse;
}

.App-config th {
  text-align: right;
  font-size: 12px;
  padding: 12px;
}

.App-config td {
  font-size: 14px;
  padding: 12px;
}

.App-config input {
  padding: 8px 8px;
  font-size: 14px;
  border: none;
  border-bottom: 1px solid black;
}

.App-config input:focus {
  outline: 0;
  font-size: 14px;
  border-bottom: 1px solid rgb(0,128,255);
}

.App h2 {
  margin: 20px 0 40px 0;
}

.App button {
  font-size: 16px;
  padding: 12px;
  min-width: 72px;
  border-radius: 4px;
  cursor: pointer;
}

.App button.primary {
  color: white;
  background-color: rgb(0,128,255);
  border: none;
}

.App button.secondary {
  color: rgb(0,128,255);
  border: 1px solid rgb(0,128,255);
  margin-left: 12px;
}

.App button:hover {
  opacity: 0.5;
}

.App .icon-button {
  cursor: pointer;
}

.App .icon-button:hover {
  opacity: 0.5;
}


